import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import PollIcon from '@material-ui/icons/Poll';
import ListAltIcon from '@material-ui/icons/ListAlt';
import { Dashboard } from '@material-ui/icons';
import DashboardInner from '../containers/DashboardInner/DashboardInner';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: 'hidden',
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9) + 1,
        },
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
}));

export default function MiniDrawer() {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div className={classes.root} >
            <CssBaseline/>
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
                
            >
                <Toolbar style={{ background: '#90CAF9' }}>
                    <IconButton
                        color="black"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, {
                            [classes.hide]: open,
                        })}

                        style={{ color: 'black' }}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap style={{color: 'black', fontWeight: 500}}>
                        Dashboard
                </Typography>
                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
                
                
            >
                <div className={classes.toolbar} style={{ background: '#424242'}}>
                    <IconButton onClick={handleDrawerClose} style={{ color: '#fff' }}>
                        {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                    </IconButton>
                </div>
                <Divider />
                <List style={{background: '#424242'}}>
                    
                    <ListItem button key="Dashboard">
                        <ListItemIcon style={{ color: 'white' }}>{<DashboardIcon/>}</ListItemIcon>
                            <ListItemText primary="Dashboard" style={{ color: 'white' }}/>
                        </ListItem>
                    <ListItem button key="Orders">
                            <ListItemIcon style={{ color: 'white' }}>{<ShoppingCartIcon/>}</ListItemIcon>
                            <ListItemText primary="Orders" style={{ color: 'white' }}/>
                        </ListItem>
                    <ListItem button key="Customers">
                        <ListItemIcon style={{ color: 'white' }}>{<PeopleIcon/>}</ListItemIcon>
                            <ListItemText primary="Customers" style={{ color: 'white' }}/>
                        </ListItem>
                    <ListItem button key="Reports">
                            <ListItemIcon style={{ color: 'white' }}>{<PollIcon/>}</ListItemIcon>
                            <ListItemText primary="Reports" style={{ color: 'white' }}/>
                        </ListItem>
                    <ListItem button key="Integraions">
                        <ListItemIcon style={{ color: 'white' }}>{<ListAltIcon/>}</ListItemIcon>
                            <ListItemText primary="Integrations" style={{ color: 'white' }}/>
                        </ListItem>
                    
                </List>
                <Divider />
                <List>
                    
                        <ListItem button key="Saved reports" disabled>
                            <ListItemIcon style={{ color: 'white' }}></ListItemIcon>
                        <ListItemText primary="Saved reports" style={{ color: 'white', fontWeight: 900  }}/>
                        </ListItem>
                        <ListItem button key="Current month">
                            <ListItemIcon style={{ color: 'white' }}>{<ListAltIcon />}</ListItemIcon>
                            <ListItemText primary="Current month" style={{ color: 'white' }}/>
                        </ListItem>
                        <ListItem button key="Last quarter">
                            <ListItemIcon style={{ color: 'white' }}>{<ListAltIcon />}</ListItemIcon>
                            <ListItemText primary="Last quarter" style={{ color: 'white' }}/>
                        </ListItem>
                        <ListItem button key="Year-end sale">
                            <ListItemIcon style={{ color: 'white' }}>{<ListAltIcon />}</ListItemIcon>
                            <ListItemText primary="Year-end sale" style={{ color: 'white' }}/>
                        </ListItem>
                    
                </List>
            </Drawer>
            <main className={classes.content} style={{ background: '#121212', height: '100vh', boxSizing: 'border-box', padding: '100px 300px'}}>
                <div className={classes.toolbar} />
                <Typography paragraph>
                    <DashboardInner />
                </Typography>
                
            </main>
        </div>
    );
}
